.Article {
  margin-bottom: 35px;
  .img {
    background-size: 100%;
    width: 100%;
    height: 30vh;
    background-repeat: no-repeat;
  }

  .content {
    position: relative;
    bottom: 75px;
    margin: 0 75px;
    padding: 35px 75px;
    background: #FFFFFF;
    border: 1px solid #EAEAEA;
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.05);
    border-radius: 5px;

    .title {
      font-size: 24px;
      color: #363636;
      margin-bottom: 50px;
      text-align: center;
    }

    .description {
      font-size: 18px;
      line-height: 150%;
    }

  }

  .back {
    margin-left:  150px;
    font-weight: 700;
    font-size: 16px;
      .back-text{
        margin-left: 5px;
      }
  }
  a{
    color: #363636;
    text-decoration: none;
  }
}